  padding: 5px;
  border: solid 1px #ccc;
  text-align: center;
  cursor: pointer;
  display: block;
  margin-bottom: 10px; }

#Page-Home #fileInput {
  display: none; }

#Page-Home #file-info {
  padding: 5px;
  margin-top: -10px;
  text-align: center; }
  #Page-Home #file-info .file-name {
    font-style: italic; }

#Page-Home #list-item .data-table {
